.hero {
  position: relative;
  margin-bottom: 30px;
  padding: 0;

  &__descr {
    // opacity: 0;
    // transform: translateY(50px);
    will-change: transform, opacity;
  }

  &__content {
    // opacity: 0;
    // transform: translateY(50px);
    will-change: transform, opacity;
  }

  &__list {
    justify-content: space-between;
    width: 100%;
  }

  &__label {
    font-size: 14px;
    text-transform: uppercase;
  }

  &__title-container {
    flex-direction: column;
    align-items: flex-end;
    margin-bottom: 30px;
  }

  &__title {
    display: block;
    margin: 0;
    font-family: "Impact", sans-serif;
    font-size: 23.9vw;
    line-height: 1;
    text-align: right;
    text-transform: uppercase;

    &--sm {
      font-size: 145px;
    }
  }

  &__text {
    margin-left: auto;
    max-width: 341px;
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
  }

  &__link {
    display: flex;
    align-items: center;
    text-transform: uppercase;
  }

  &__icon {
    margin-left: 15px;
    width: 16px;
    height: 16px;
  }

  &__img {
    position: absolute;
    left: -400px;
    bottom: -450px;
    z-index: 1;
    width: 100%;
    // opacity: 0;
    transform: scale(0.9);
  }
}
